import { DateTime } from "luxon";
import { sleep } from "@/utils/SystemUtils";
import {
  canEditForm,
  hasEditForms,
  isEditableStatus,
  isRevisionForm,
  isRevisionFormIncludes,
  isRevisionStatus,
} from "@/helpers/StatusHelpers";
import { FormsTabKeys, FormStatus, FormTypes } from "@/constants/FormConstants";

export const formInfoModule = {
  namespaced: true,
  state: {
    name: "",
    revisionForms: [FormsTabKeys.KeyDates],
    savedAt: undefined,
    type: FormTypes.Tech,
    status: FormStatus.Signed,
  },
  getters: {
    getStatus(state) {
      return state.status;
    },
    getRevisionForms(state) {
      return state.revisionForms;
    },
    hasEditForms(state) {
      return hasEditForms(state.status, state.revisionForms);
    },
    getFormStatus(state) {
      return (formKey) => {
        if (isRevisionForm(formKey, state.status, state.revisionForms)) {
          return FormStatus.Revision;
        }

        if (
          isRevisionStatus(state.status) &&
          !isRevisionFormIncludes(formKey, state.revisionForms)
        ) {
          return FormStatus.Signed;
        }

        return state.status;
      };
    },
    canEditForm(state) {
      return (formKey) =>
        canEditForm(formKey, state.status, state.revisionForms);
    },
    getSavedAt(state) {
      return state.savedAt;
    },
    getName(state) {
      return state.name;
    },
    getFormType(state) {
      return state.type;
    },
    isEditableStatus(state) {
      return isEditableStatus(state.status);
    },
    getType(state) {
      return state.type;
    },
  },
  mutations: {
    setFormName(state, name) {
      state.name = name;
    },
    setFormStatus(state, status) {
      state.status = status;
    },
    changeFormType(state, type) {
      state.type = type;
    },
    changeSavedAt(state, value) {
      state.savedAt = value;
    },
    setForm(state, data) {
      state.name = data.name;
      state.type = data.type;
      state.status = data.status;
      state.savedAt = data.savedAt;
    },
  },
  actions: {
    async saveForms(store) {
      // Имитация запроса на сервер
      await sleep();

      store.commit("changeSavedAt", DateTime.now().toISO());
    },
  },
};
