import { DateTime } from "luxon";

export const applicationModule = {
  namespaced: true,
  state: {
    language: "ru",
  },
  getters: {
    getApplicationLanguage(state) {
      return state.language;
    },
    getToken(state) {
      return state.token;
    },
    getWeekdays(state) {
      const dateTime = DateTime.now().setLocale(state.language);

      return [
        dateTime.set({ weekday: 1 }).weekdayShort,
        dateTime.set({ weekday: 2 }).weekdayShort,
        dateTime.set({ weekday: 3 }).weekdayShort,
        dateTime.set({ weekday: 4 }).weekdayShort,
        dateTime.set({ weekday: 5 }).weekdayShort,
        dateTime.set({ weekday: 6 }).weekdayShort,
        dateTime.set({ weekday: 7 }).weekdayShort,
      ];
    },
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
    changeApplicationLanguage(state, language) {
      state.language = language;
    },
  },
};
