import { FormsTabStatus } from "@/constants/FormConstants";

export function getFormStatus({ isFilled, isNotFilled }) {
  if (isFilled) {
    return FormsTabStatus.Filled;
  }

  if (isNotFilled) {
    return FormsTabStatus.NotFilled;
  }

  return FormsTabStatus.NotFullyFilled;
}

export function formatOtherAuthorErrors(fields = {}, localize = {}) {
  const errors = {};

  if (!fields.russianName) {
    errors.russianName = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishName) {
    errors.englishName = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianWorkplace) {
    errors.russianWorkplace = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishWorkplace) {
    errors.englishWorkplace = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianDivision) {
    errors.russianDivision = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishDivision) {
    errors.englishDivision = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianPosition) {
    errors.russianPosition = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishPosition) {
    errors.englishPosition = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianOtherCitizenship) {
    errors.russianOtherCitizenship =
      localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishOtherCitizenship) {
    errors.englishOtherCitizenship =
      localize.commonFormRusTextFillingError.title;
  }

  if (!fields.birthday) {
    errors.birthday = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianRegisterAddress) {
    errors.russianRegisterAddress =
      localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishRegisterAddress) {
    errors.englishRegisterAddress =
      localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianPostAddress) {
    errors.russianPostAddress = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishPostAddress) {
    errors.englishPostAddress = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.inn && fields.isCitizen) {
    errors.inn = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.snils && fields.isCitizen) {
    errors.snils = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.passportSeries && fields.isCitizen) {
    errors.passportSeries = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.passportNumber && fields.isCitizen) {
    errors.passportNumber = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.passportIssueDate && fields.isCitizen) {
    errors.passportIssueDate = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.passportDivisionCode && fields.isCitizen) {
    errors.passportDivisionCode = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.russianPassportIssued && fields.isCitizen) {
    errors.russianPassportIssued = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.englishPassportIssued && fields.isCitizen) {
    errors.englishPassportIssued = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.phoneNumber) {
    errors.phoneNumber = localize.commonFormRusTextFillingError.title;
  }

  if (!fields.emailAddress) {
    errors.emailAddress = localize.commonFormRusTextFillingError.title;
  }

  return errors;
}

