        Desc: "Укажите в таблице ниже сведения о внешних (любых, кроме Института) источниках финансирования. Если при создании Технологии финансирование из внешних источников не осуществлялось, поставьте отметку «Неприменимо». \n Просим обратить внимание на важность точного и полного указания сведений об источниках финансирования (например, о договорах, в рамках которых были предоставлены грант, субсидия, иное финансирование, о проекте, в целях реализации которого они предоставлены, о лицах, их предоставивших и т.п.). Эти сведения будут использованы для установления наличия лиц, обладающих правами на Технологию, а также для определения соответствия Технологии требованиям финансируемого проекта и применимого права. ",
